
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7, 17, 27, 0.5);
}
.header .content-wrapper {
    position: relative;
    padding: 24px 12px 18px 24px;
    font-size: 0;
}
.header .content-wrapper .avatar {
      display: inline-block;
      vertical-align: top;
}
.header .content-wrapper .avatar img {
        border-radius: 2px;
}
.header .content-wrapper .content {
      display: inline-block;
      margin-left: 16px;
}
.header .content-wrapper .content .title {
        margin: 2px 0 8px 0;
        width: 100%;
}
.header .content-wrapper .content .title .brand {
          display: inline-block;
          width: 36px;
          height: 18px;
          vertical-align: top;
          background-image: url(brand@2x.png);
          background-size: 30px 18px;
          background-repeat: no-repeat;
}
@meida (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url(brand@3x.png);
}
}
.header .content-wrapper .content .title .name {
          display: inline-block;
          margin-left: 6px;
          font-size: 16px;
          line-height: 18px;
          font-weight: bold;
          width: 100%;
}
.header .content-wrapper .content .discription {
        margin-bottom: 10px;
        line-height: 12px;
        font-size: 12px;
}
.header .content-wrapper .content .support .icon {
        display: inline-block;
        vertical-align: top;
        width: 12px;
        height: 12px;
        margin-right: 4px;
        background-size: 12px 12px;
        background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
          background-image: url(decrease_1@2x.png);
}
@meida (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
