.header .bulletin-wrapper .bulletin-text {
      vertical-align: top;
      margin: 0 4px;
      fontsize: 10px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
      position: absolute;
      font-size: 10px;
      right: 12px;
      top: 8px;
}
.header .background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-filter: blur(10px);
            filter: blur(10px);
}
.header .detail {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    opacity: 1;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    background: rgba(7, 17, 27, 0.8);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}
.header .detail.fade-enter-active {
      background: rgba(7, 17, 27, 0.8);
}
.header .detail.fade-enter, .header .detail.fade-leave-active {
      opacity: 0;
      background: rgba(7, 17, 27, 0);
}
.header .detail .detail-wrapper {
      width: 100%;
