
.star {
  font-size: 0;
}
.star .star-item {
    display: inline-block;
    background-repeat: no-repeat;
}
.star .star-item:last-child {
      margin-right: 0;
}
.star.star-48 .star-item {
    width: 20px;
    height: 20px;
    margin-right: 22px;
    background-size: 20px 20px;
}
.star.star-48 .star-item:last-child {
      margin-right: 0;
}
.star.star-48 .star-item.on {
      background-image: url(star48_on@2x.png);
}
@meida (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
.star.star-48 .star-item.on {
    background-image: url(star48_on@3x.png);
}
}
.star.star-48 .star-item.half {
      background-image: url(star48_half@2x.png);
}
@meida (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
